* {
  margin: 0;
  padding: 0;
}
html {
  font-size: 62.5%;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12rem;
  color: #f1f1f1;
  padding: 3.6rem 6.4rem;
}
h1 {
  font-size: 3.2rem;
  cursor: pointer;
}
header ul {
  list-style: none;
  display: flex;
  align-items: center;
  gap: 2.4rem;
  font-size: 2.4rem;
}
header a {
  color: #f1f1f1;
  text-decoration: none;
  transition: 0.3s;
}
nav ul li a.active {
  color: #ff7f50;
}
header a:hover {
  color: #ffffffb3;
}
header a:active {
  color: #ff7f50;
}
@media (max-width: 1024px) {
  header {
    gap: 3.2rem;
  }
  h1 {
    font-size: 2.4rem;
  }
  header ul {
    font-size: 1.8rem;
  }
}
@media (max-width: 768px) {
  header {
    padding: 1.6rem 2.4rem;
  }
  h1 {
    font-size: 1.8rem;
  }
  header ul {
    font-size: 1.6rem;
  }
}
@media (max-width: 475px) {
  header h1 {
    display: none;
  }
  nav {
    width: 100%;
  }
  header ul {
    justify-content: space-between;
    font-size: 2rem;
    text-align: center;
  }
}
