footer {
  color: #f1f1f1;
  padding: 3.6rem 6.4rem;
  display: flex;
  justify-content: space-between;
  gap: 3.2rem;
}
footer a {
  color: #f1f1f1;
  text-decoration: none;
  font-size: 1.4rem;
  transition: 0.3s;
}
footer h6 {
  font-size: 1.8rem;
  color: #ff7f50;
}
footer ul {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  gap: 1rem;
  font-size: 2.4rem;
  list-style: none;
}
footer a:hover {
  color: #ffffffb3;
}
footer a:active {
  color: #ff7f50;
}
@media (max-width: 1024px) {
  footer ul {
    font-size: 1.8rem;
  }
}
@media (max-width: 768px) {
  footer {
    padding: 1.6rem 2.4rem;
    text-align: center;
    justify-content: space-around;
  }
  footer ul {
    font-size: 1.4rem;
    align-items: center;
    text-align: center;
  }
  footer h6 {
    font-size: 1.6rem;
  }
  .TTD-container {
    display: none;
  }
}
