main {
  padding: 3.6rem 6.4rem;
  display: flex;
}
.section-container {
  display: flex;
  justify-content: center;
  width: 100%;
}
.content {
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.4rem;
  justify-content: center;
}
.content p {
  color: #ffffffb3;
  font-size: 1.8rem;
}
.content h1 {
  color: #ff7f50;
  font-size: 3.6rem;
}

.popular ul {
  list-style: none;
  display: flex;
  gap: 2.4rem;
  height: 100%;
}

.popular li {
  width: 33.3%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.4rem;
}
.popular .img-container {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.popular img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.popular p {
  color: #ffffffb3;
  font-size: 2.4rem;
  text-align: center;
}
.popular img:hover {
  transform: scale(1.1);
  transition: 0.5s;
  cursor: pointer;
}
.culture {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.culture img {
  width: 50%;
  height: 70%;
  object-fit: contain;
}
@media (max-width: 1024px) {
  .content {
    width: unset;
  }
}
@media (max-width: 768px) {
  main {
    padding: 1.6rem 2.4rem;
  }
  .content p {
    color: #ffffffb3;
    font-size: 1.6rem;
  }
  .content h1 {
    color: #ff7f50;
    font-size: 2.4rem;
  }
}
